/**
 * Hook para inicializar a aplicao
 * Carrega apenas os medidores pr-configurados (sem dados de produo mockados)
 * Os dados de produo devem ser importados via PDF pelo usurio
 */

import { useEffect, useRef } from 'react'
import { useAppStore } from '@/store/useAppStore'
import { realMeters, realCalibrationPVT } from '@/data/realData'

export function useInitializeRealData() {
  const meters = useAppStore((state) => state.meters)
  const initialized = useRef(false)

  useEffect(() => {
    // S executa uma vez
    if (initialized.current) return
    initialized.current = true

    // Inicializa medidores se estiver vazio
    if (meters.length === 0) {
      console.log('[MPFM] Inicializando medidores do FPSO Bacalhau...')
      realMeters.forEach((meter) => {
        // Usa set direto para no gerar alertas na inicializao
        useAppStore.setState((state) => ({
          meters: [...state.meters, { ...meter, id: `meter-${meter.tag}` }],
        }))
      })
    }

    // NO carrega dados de monitoramento mockados
    // O usurio deve importar os PDFs diariamente
    console.log('[MPFM] Inicializao concluda! Importe PDFs para popular dados de produo.')
  }, [meters.length])
}

// Dados de calibrao PVT para uso no workflow
export function useCalibrationPVTData() {
  return realCalibrationPVT
}
